.home_cover{background-image:url(/street-car-cafe/static/media/background-home.492b6727.jpg);background-repeat:no-repeat;background-position:center;background-attachment:fixed;display:flex;justify-content:center;height:90vh}.home_image-box{display:flex;align-items:center;width:20rem}.home_image-box img{width:inherit}.home_about{margin:2rem 4rem 0;display:grid;grid-template-columns:1fr 1fr;grid-column-gap:3rem;-webkit-column-gap:3rem;column-gap:3rem;grid-row-gap:3rem;row-gap:3rem}@media (max-width: 56.25em){.home_about{grid-template-columns:1fr;margin:1rem 2rem 0}}.home_about-name-container{grid-column:1/3;display:flex;align-items:center;flex-direction:column}@media (max-width: 56.25em){.home_about-name-container{grid-column:1}}.home_about-image{width:100%}.home_about-image img{width:100%;height:100%}.home_about-text-container{display:flex;align-items:center;flex-direction:column;justify-content:center;padding:2rem}.home_image-gallery{display:grid;margin:2rem;grid-template:1fr 1fr / 1fr 1fr 1fr;grid-gap:2rem;gap:2rem}@media (max-width: 75em){.home_image-gallery{grid-template:1fr 1fr / 1fr 1fr}}@media (max-width: 37.5em){.home_image-gallery{grid-template:1fr 1fr / 1fr}}.home_image-item{width:100%;height:30rem;box-shadow:3px 3px 10px #000}.home_image-item img{width:100%;height:100%;object-fit:cover}

.section_heading{display:grid;place-items:center;margin:1rem 2rem}.section_heading h1{font-size:4rem;font-weight:bold}.section_heading-line{background-color:#e4130e;height:0.3rem;width:15%;margin-top:1rem}@media (max-width: 37.5em){.section_heading-header{display:flex;flex-direction:column;align-items:center;margin-bottom:1rem}}

.button{background-color:#e4130e;padding:1rem;font-size:1.5rem;border:none;align-self:flex-end;text-transform:uppercase;border-radius:0.3rem;cursor:pointer;outline:none;margin-top:2rem}.button a{color:#fff;text-decoration:none}.button .button:hover{background-color:rgba(0,0,0,0.8)}

.nav_logo-box{margin:0.5rem 0 0.3rem 0}.nav_logo-box img{height:7rem;border-radius:50%}

.nav_container{display:flex;padding:0 0.5rem}.nav_container a{text-decoration:none;color:#fff;padding:3rem 2rem}.nav_container a:hover{color:rgba(255,193,7,0.7)}.nav_links-active{color:#ffc107 !important}@media only screen and (max-width: 767px){.nav_container{flex-direction:column;width:100%}.nav_container a{padding:0;display:inline-block;width:100%;height:100%}.nav_link{margin-bottom:2rem;width:100%;text-align:center}}

.sidebar{position:fixed;top:0;right:0;height:100vh;width:40vw;background-color:#000;display:flex;align-items:center;justify-content:space-around}

.nav__hamburger--button{display:inline-block;cursor:pointer;z-index:100;background-color:#000;outline:none;border:none}.nav__hamburger--bar1,.nav__hamburger--bar2,.nav__hamburger--bar3{width:35px;height:5px;background-color:#fff;margin:6px 0;transition:0.4s}.nav__hamburger--change .nav__hamburger--bar1{transform:rotate(-45deg) translate(-9px, 6px)}.nav__hamburger--change .nav__hamburger--bar2{opacity:0}.nav__hamburger--change .nav__hamburger--bar3{transform:rotate(45deg) translate(-8px, -8px)}

.modal{position:absolute;top:0;left:0;height:100vh;width:100vw;background-color:rgba(0,0,0,0.5)}

.nav{padding:0 2rem;display:flex;justify-content:space-between;align-items:center;background-color:#000;text-transform:uppercase;position:fixed;top:0;width:100%}

.footer{background-color:rgba(0,0,0,0.8);color:rgba(255,255,255,0.7);display:flex;justify-content:center}.footer p{align-self:center}.footer p a{color:inherit;font-weight:bold}.footer p div{display:inline;size:1rem;padding:0 0.5rem}

*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}@media (max-width: 75em){html{font-size:56.25%}}@media (max-width: 56.25em){html{font-size:50%}}@media (max-width: 112.5em){html{font-size:75%}}body{box-sizing:border-box;font-size:1.6rem;font-family:'Montserrat', sans-serif}

